.modal {
  z-index: 100;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba($color: #000000, $alpha: 0.35);
  display: flex;
  justify-content: center;
  padding-top: 5rem;
}

.modalBody {
  padding: 2rem;
  width: 420px;
  border-radius: 5px;
  background: rgba($color: #fff, $alpha: 1);
  height: fit-content;
  display: flex;
  flex-direction: column;
  align-items: center;

  & .title {
    text-transform: capitalize;
  }

  & .form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }

  & .btns {
    padding-top: 15px;
  }
}